<script>
	import tPayWay from "@/components/t-pay-way/ti-pay-way"
    import {mapMutations,mapState,mapGetters} from 'vuex';
	export default {
		components: {
			tPayWay
		},
        computed: {
            ...mapState(['modalAction']),
        },
		data() {
			return {
				hasCoupon: false,
				insufficient: false,
				show: false,
				listData: {},
				goods: {},
                remark: '',
				payInfo:'',
                credit:0,
                addressData:'',
			}
		},
		onLoad(){
		    this.getList()
		},
		methods: {
            getList:function () {
                const params = {}
                if(this.$Route.query.goodsid) params.id = this.$Route.query.goodsid || false
                if(this.$Route.query.total) params.total = this.$Route.query.total || 1
                if(this.$Route.query.optionid) params.optionid = this.$Route.query.optionid
                this.$http.get('order.create',params).then((res)=>{
                    this.listData = res
                    this.goods = res.goods
                    this.addressData = res.address
                })
            },
			chooseAddrCallback(addressData) {
                this.addressData = addressData
			},
            submit() {
                if(!this.addressData.id){
                    this.$utils.toast('请选择收货地址')
					return;
				}
                const params = {goods:this.goods[0]['goods'],addressid:this.addressData.id,remark:this.remark,hasinvoice:false}
                if(!this.$Route.query.goodsid) params.fromcart = 1
                this.$http.post('order.create.submit',params).then((res)=>{
					if(res.error == 0){
					    this.actionPay(res.orderid)
					}
				})
			},
            payment:function (params) {
				switch (params.type){
					case 'credit':
					    this.$http.get('order.pay.complete',{id:this.payInfo.order.id,type:params.type}).then((res)=>{
							if(res.error==0){
                                this.$utils.toast('支付成功')
                                this.show = false
							    //this.$Router.push({name:'order_detail',query:{orderid:this.payInfo.order.id}})
                                this.$Router.push({path:'/pages/order/pay/result',query:{type:'order',orderid:this.payInfo.order.id}})
							}else{
                                this.$utils.toast('支付出错')
							}
						})
						break;
                    case 'wechat':
                        this.$utils.weixinPay(this.payInfo.wechat.payinfo).then((res)=>{
                            if(res!==false) this.$utils.toast('支付成功')
                            this.show = false
                            if(res!==false) this.$Router.push({path:'/pages/order/pay/result',query:{type:'order',orderid:this.payInfo.order.id}})
                            else setTimeout(()=>{this.$Router.push({path:'/pages/order/detail',query:{id:this.payInfo.order.id,listener:true}})},1500)
                        }).catch((err)=>{
                            this.$utils.toast('支付失败')
                        });
                        break;
					default:
					    this.$utils.toast('请先选择支付方式')
						return;
					        break;
				}
            },
            actionPay:function (orderid) {
				this.$http.get('order.pay',{id:orderid}).then((res)=>{
					this.payInfo = res
					this.credit = parseInt(res.credit.current)
                    this.show = true
				})
            },
			popupClose() {
				this.$utils.modal.confirm('确定取消支付吗？').then((res)=>{
                    this.show = false
					//setTimeout(()=>{this.$Router.replace({path:'/pages/order/detail',query:{id:this.payInfo.order.id}})},300)
				})
			}
		}
	}
</script>

